import React from 'react';
import './Button.css';


class Button extends React.Component {
    handleClick() {
      this.props.onClick()
    }
  
    render() {
      // This syntax ensures `this` is bound within handleClick
      return (
        <button class="App-link" onClick={() => this.handleClick() }>
          Click me
        </button>
      );
    }
  }

  export default Button;